import about from "./about";
import faq from "./faq";
import media from "./media";
import contacts from "./contacts";
import home from "./home";
import trivia from "./trivia";
trivia();

function select_tab(id) {
  document
    .querySelectorAll(".route")
    .forEach((item) => item.classList.remove("selected"));
  document
    .querySelectorAll(id)
    .forEach((item) => item.classList.add("selected"));
}
function load_content(id) {
  var content = document.querySelector("#main");
  if (id == "home") {
    home();
  } else if (id == "about") {
    about();
  } else if (id == "media") {
    media();
  } else if (id == "faq") {
    faq();
  } else if (id == "contacts") {
    contacts();
  } else {
    content.innerHTML = `Error`;
  }
}
function push(event) {
  let id = event.target.id;
  select_tab(id);
  document.title = id;
  load_content(id);
  // window.history.pushState({ id }, `${id}`, `/${id}`);
  window.history.replaceState({ id }, `${id}`, `/${id}`);
}

window.onload = (event) => {
  window["home"].addEventListener("click", (event) => push(event));
  window["about"].addEventListener("click", (event) => push(event));
  window["media"].addEventListener("click", (event) => push(event));
  window["faq"].addEventListener("click", (event) => push(event));
  window["contacts"].addEventListener("click", (event) => push(event));
};
